<template>
 <div class="coming" ref="coming" name="coming">

    <div class="movieitem clearfix" v-for="(item,index) in comingMovieData"
      :key="index" ref="movieitem" :id="item.id" @click="viewMovieDetail({name: 'movieDetail', params: {id: item.id}})">
        <div class="fl movieimg">
          <img class="autoimg" :src="item.images.small" alt />
        </div>
        <div class="fl movieinfo">
          <div class="one-txt">{{item.title}}</div>
          <div class="one-txt">
            <span>橙子影视评分：</span>
            <span class="">{{item.rating.average}}分</span>
          </div>
          <div class="one-txt">
            <span>导演：</span>
            <span>{{item.directors[0].name}}</span>
          </div>
          <div class="one-txt">
            <span>主演：</span>
            <span>{{item.avatarsInfo}}</span>
          </div>
          <div class="one-txt ">
            <span>{{item.genresInfo}}</span>
          </div>
        </div>
        <div class="fl buyinfo">
          <div class="buybtn" @click.stop="buyTickets(item.id)">
            <!-- color="linear-gradient(to right, #FF429B, #FF3472)" -->
            <van-button round size="small" type="info">预售</van-button>
          </div>
          <!-- <div>特惠</div> -->
        </div>
      </div>
      <!-- <p class="notice" v-if="!isHas">已经没有更多数据可以加载了！</p> -->
    </div>
</template>

<script>
    import { createNamespacedHelpers } from "vuex";
    const { mapState } = createNamespacedHelpers("comingModule");
    export default {
        name:'coming',
        data(){
            return{

            }
        },

        computed:{
            ...mapState(['comingMovieData'])
        },
        created(){
            this.axios({
                method: 'GET',
                url: 'https://douban.uieee.com/v2/movie/coming_soon'
            }).then(result => {

                // console.log('result.data.subjects ==> ', result.data.subjects);

                result.data.subjects.forEach(v => {
                
                  v.genresInfo = v.genres.join(" | "); //合并电影类型格式如 剧情|犯罪
                  // console.log('v.genresInfo=>',v.genresInfo);

                  v.avatarsInfo = ""; // 合并演员
                  v.casts.forEach(item => {
                    v.avatarsInfo += item.name + " / "; //如 胡歌/桂纶镁/廖凡
                });

                  v.avatarsInfo = v.avatarsInfo.slice(0,-3); //去掉最后一个名字的“ /” 根据自己所需格式来截取
                  // console.log('v.avatarsInfo=>',v.avatarsInfo);
                });
                this.$store.commit('comingModule/getComingData',result.data.subjects);


            }).catch(err => {
                // console.log('err ==> ', err);
            })

        },

        methods:{

          //查看电影详情
          viewMovieDetail(o) {
            this.$router.push(o);
          },

          // 购票
          buyTickets(id){

            //获取用户登录状态
            let userStatus = localStorage.getItem('user');

            // 判断是否是登录状态
            if (userStatus){
              userStatus = JSON.parse(userStatus);

              // 没有登录
              if (!userStatus.isLogin) {
                Dialog.confirm({
                    title: '您还没有登录不能购买该电影哦！',
                    message:'确定将自动跳转到登录页面'
                    }).then(() => {
                      return this.$router.push({name: 'login'});
                }).catch(() => {
                  // on cancel
                });
              }

              // 已经登录
              this.$router.push({name:'buyTickets',query:{id}});
              
            }else{

              Dialog.confirm({
                  title: '您还没有登录不能购买该电影哦！',
                  message:'确定将自动跳转到登录页面'
                  }).then(() => {
                    return this.$router.push({name: 'login'});

                }).catch(() => {
                  // on cancel
                });
              }
              this.$toast.clear();  
          },
        }
    }
</script>

<style lang="less" scoped>
 .coming{
        // height: calc(~"100%" - 100px);
        margin-top: 1px;

        position: absolute;
        top: 44px;
        bottom: 0;
        left: 0;
        right: 0;
        overflow-y: auto;
      }
      .movieitem{
        margin-bottom: 8px;
        background-color: #fff;

        .movieimg{
          width: 80px;
          height: 120px;
          background-color:#f5f5f5;
          border-radius: 8px;
          margin-top: 11px;

          .autoimg{
            border-radius: 8px;
          }
        }

        .movieinfo{
          width: calc(~"100% - 160px - 20px");
          padding: 0 10px;
          text-align: left;
          color: #888;
          > div {
            height: 28px;
            line-height: 28px;
            &:nth-child(1) {
              font-size: 18px;
              color: #333;
            }
            &:nth-child(2) {
              font-size: 15px;
              color: #FF9C1C;
            }
            &:nth-child(3) {
              font-size: 16px;
            }
            &:nth-child(4) {
              font-size: 16px;
            }
            &:last-child{
              margin-top: 4px;
              >span{
                color: #333;
                font-size: 13px;
                border:1px solid #b0b0b0;
              }
            }

          }
        }
        .buyinfo {
          width:70px;
          text-align: center;
          margin-top: 45px;
          color: #808080;
          font-size: 13px;
          
          .buybtn {
          height: 40px;
          }
        }

      }

      .notice{
        padding: 0;
        color: #aaa;
        text-align: center;
        padding: 10px 0;
        font-size: 12px;
        background-color: #f5f5f5;
      }
</style>